$primaryColor: #4527a4;
$secondaryColor: #8344c5;

.csbInspectorInfo {
	padding: 6px;

	a {
		text-decoration: none;
		font-weight: bold;
	}
}

.block-editor-block-list__block[data-type='csb/content-slider-block'][data-align='wide'] {
	max-width: 1440px !important;
	width: 94% !important;
	margin: 0 auto;

	@media screen and (max-width: 1366px) {
		width: 96% !important;
	}

	@media screen and (max-width: 768px) {
		width: 98% !important;
	}

	.wp-block {
		max-width: 100% !important;
		width: 100% !important;
	}
}

// For Pattern
.block-editor-inserter__panel-content {
	.block-editor-block-preview__content {
		min-height: 176px;

		>iframe {
			min-height: 706px;

			.block-editor-block-preview__content-iframe {
				height: auto;
			}
		}
	}
}

// Content Slider Block
.wp-block-csb-content-slider-block {
	.csbContentSlider {
		.swiper-slide {

			.sliderTitle,
			.sliderDescription,
			.sliderBtn {
				cursor: move;
			}
		}
	}
}